import React from "react";
import "./FeaturedMovie.css";

export default ({ item }) => {
  let firstDate = new Date(item.first_air_date).getFullYear();
  let genres = [];
  for (let i = 0; i < item.genres.length; i++) {
    genres.push(item.genres[i].name);
  }

  let description = item.overview;
  if (description.length > 200) {
    description = description.split(" ").slice(0, 30).join(" ") + " ...";
  }
  return (
    <section
      className="featured"
      style={{
        backgroundSize: "cover",
        backgroundPosition: "center",
        backgroundImage: `url(https://image.tmdb.org/t/p/original/${item.backdrop_path})`,
      }}
    >
      <div className="featured--vertical">
        <div className="featured--horizontal">
          <div className="featured--name">{item.original_name}</div>
          <div className="featured--info">
            <div className="featured--points">{item.vote_average} pontos</div>
            <div className="featured--year">{firstDate}</div>
            <div className="featured--seasons">
              {item.number_of_seasons} temporada
              {item.number_of_seasons !== 1 ? "s" : ""}
            </div>
          </div>
          <div className="featured--description">{description}</div>
          <div className="featured--buttons">
            <a href={`/watch/${item.id}`}>
              <span className="arrow--play"></span> Assistir
            </a>
            <a href={`/list/add/${item.id}`}>+ Minha Lista</a>
          </div>
          <div className="featured--genres">
            <b>Gneros:</b> {genres.join(", ")}
          </div>
        </div>
      </div>
      {/* <div>{item.original_name}</div> */}
    </section>
  );
};
